@import url('https://fonts.googleapis.com/css2?family=Kufam&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Kufam', cursive;
}
.container {
  max-width: 1100px;
  margin: 0 auto; 
  padding: 0 auto;
  overflow: auto;
}
@media (min-width: 786px) {
  .container {
    max-width: 100%;
    padding: 0 auto;
    margin: 0 auto;
  }
}
