@import url(https://fonts.googleapis.com/css2?family=Kufam&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Kufam', cursive;
}
.container {
  max-width: 1100px;
  margin: 0 auto; 
  padding: 0 auto;
  overflow: auto;
}
@media (min-width: 786px) {
  .container {
    max-width: 100%;
    padding: 0 auto;
    margin: 0 auto;
  }
}


.ant-btn-circle{
    margin-left: 0.5rem;
}
.ant-card.ant-card-bordered {
    margin: 1.5rem 3rem;
    background-color: #fbec5d;
    color:#1890ff;
    max-width: 580px;
  }
  
  .ant-card-extra .ant-btn {
    background-color: #fbec5d;
    border: 1.5px solid #1890ff;
  }


.ant-card-head-title{
  text-align: start;
  font-size: 1.6rem;
  font-weight: bold;
  color:#1890ff;
  white-space: unset;  

}
  .ant-card-head-title::first-letter{
    font-size: 2em;
}
  .card-description{
      text-align: justify;
      font-size: 1rem;
      color:#1890ff; 
      white-space: unset;  
    }

  .card-date{
      text-align: end;
      font-size: smaller;
      margin: 0;
  }

.ant-tooltip-open{
  background-color: #1890ff;
}


@media (max-width: 786px) {
  .ant-card-head-title{
    font-size: 1.4rem;
    white-space: wrap;
  }
  .card-description{
    font-size: 0.8rem;
    white-space: wrap;
}
}

@media (max-width: 425px) {
  .ant-card-head-title{
    font-size: 1.2rem;
  }
  .card-description{
    font-size: 0.6rem;
}
}
#title.ant-input{
    margin-bottom: 0.5rem;
}

#description.ant-input{
    margin-top: 0.5rem;
}

.ant-modal-body{
    padding-top: 1rem;
    padding-bottom: 1rem;

}
.ant-modal-header,.ant-modal-footer{
    background-color: #fbec5d;
}
.ant-modal-header,.ant-modal-footer{
    background-color: #fbec5d;
}

.ant-drawer-header{
    background-color: #fbec5d;
    height: 5rem;
}

h2{
    font-size: 2rem;
}

h3{
    font-size: 1rem;
    font-style: italic;
}

.similar-announcement{
    margin-bottom: 10px;
    background-color: #1890ff;
    border-radius: 10px;
}

.similar-title{
    border-bottom: 1px solid #fbec5d;
    color: #fbec5d;
    padding: 1rem;
    font-size: 1rem;
}

.similar-description{
  color: #fbec5d;
  padding: 1rem;
  font-size: 0.7rem;
}

@media (max-width: 786px) {
  h2{
    font-size: 1.5rem;
  }

  h3{
    font-size: 0.9rem;
  }
  .similar-title{
    font-size: 1rem;
    white-space: pre-wrap;
  }
  .similar-description{
    font-size: 0.6rem;
    white-space: pre-wrap;
  }
}
    
@media (max-width: 425px) {
  h2{
    font-size: 1.3rem;
  }

  h3{
    font-size: 0.8rem;
  }
  .similar-title{
    font-size: 0.8rem;
  }
  .similar-description{
    font-size: 0.5rem;
  }
}

.ant-input-wrapper.ant-input-group{
    margin-left:3rem;
    margin-right:3rem;
    width: 20rem;
}
.ant-btn.ant-btn-primary.ant-btn-lg.button{
    display: block; 
    margin: 1.5rem 3rem 1.5rem;
    height: auto;
    color:#fbec5d;
}

.ant-btn-icon-only{
    color: #1890ff;
}

.ant-btn-icon-only:hover{
    background-color: #1890ff;
    color: #fbec5d;
}



.announcement-list{
  min-height: 31rem;
}

.empty-list{
  margin: 1.5rem 3rem;
  font-size: 2rem;
}

@media (max-width: 786px) {
  .empty-list{
    font-size: 1.5rem;
  }
}
    
@media (max-width: 425px) {
  .empty-list{
    font-size: 1rem;
  }
}
.header-title{
  height: 100%;
  padding-bottom: 4rem;
}
.header-title h1{
  justify-content: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  width: 100%;
  background-color: #fffffe;
  border-bottom: 3px solid #fbec5d;
  font-family: 'Merriweather Sans', sans-serif;
  font-weight: bold;
  color: #1890ff;
  font-size: 2rem;
  display: flex;
  position: fixed;
  z-index: 1000;
}

.header-main h2,.header-main p{
    text-align: center;
    margin: 0;
}

.header-main h2{
  padding-top: 2rem;
  font-size: 3.5rem;
  color: #1890ff;
  font-weight: bold;
}

.header-main p{
  padding-bottom: 2rem;
  font-size: 1.5rem;
  color: #1890ff;
}

.header-main{
  height: 100%;
  background: rgba(255,231,15,1);
  background: -webkit-gradient(left top, right top, color-stop(0%, rgba(255,231,15,1)), color-stop(22%, rgba(255,231,15,0.98)), color-stop(43%, rgba(255,231,15,0.96)), color-stop(72%, rgba(36,167,204,0.94)), color-stop(100%, rgba(0,113,219,0.94)));
  background: linear-gradient(to right, rgba(255,231,15,1) 0%, rgba(255,231,15,0.98) 22%, rgba(255,231,15,0.96) 43%, rgba(36,167,204,0.94) 72%, rgba(0,113,219,0.94) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffe70f', endColorstr='#0071db', GradientType=1 );
}

@media (max-width: 786px) {
  .header-main h2{
    font-size: 3rem;
  }
  .header-main h2,.header-main p{
    color: #fffffe;
  }
}
  
@media (max-width: 425px) {
  .header-main h2{
    font-size: 2.5rem;
  }
  
}

.footer{
  border-top: 3px solid #fbec5d;
  background-color: #fffffe;
  height: 3rem;
  display: flex;
}

.footer .footer-title{
  margin: auto;
  font-family: 'Merriweather Sans', sans-serif;
  font-size: 1rem;
  color: #1890ff;
}

@media (max-width: 786px) {
  .footer .footer-title{
    font-size: 0.8rem;
  }
}
  
@media (max-width: 425px) {
  .footer .footer-title{
    font-size: 0.6rem;
  }
}
